import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { MatDialog } from '@angular/material/dialog'
import { MatPaginator } from '@angular/material/paginator'
import { MatSort } from '@angular/material/sort'
import { MatTableDataSource } from '@angular/material/table'
import { Subscription } from 'rxjs'
import { VehicleDamage } from 'src/app/business/models/vehicleTemplate.models'
import { AdministrationService } from 'src/app/business/services/administration.service'
import { DialogService } from 'src/app/shared/dialogs/dialog.service'
import { NotificationService } from 'src/app/shared/notifications/notification.service'
import {
  VehicleDamageDialogComponent,
  VehicleDamageDialogData,
} from './vehicle-damage-dialog/vehicle-damage-dialog.component'

@Component({
  selector: 'app-vehicle-damage',
  templateUrl: './vehicle-damage.component.html',
  styleUrls: ['./vehicle-damage.component.scss'],
})
export class VehicleDamageComponent implements AfterViewInit, OnInit, OnDestroy {
  displayedColumns = ['uid', 'name', 'space', 'space2', 'actions']
  pageSizeOptions: number[] = [12, 24, 50, 100]

  @ViewChild(MatSort) sort: MatSort
  @ViewChild(MatPaginator) paginator: MatPaginator

  subscriptions: Subscription[] = []
  items: MatTableDataSource<VehicleDamage>
  loading: boolean

  constructor(
    public dialog: MatDialog,
    private dialogService: DialogService,
    public notificationService: NotificationService,
    private adminService: AdministrationService
  ) {
    this.items = new MatTableDataSource([])
    const sub = this.adminService.vehicleDamage.subscribe((data) => {
      this.items.data = data
    })
    this.subscriptions.push(sub)
  }

  ngOnInit() {
    this.dialogService.showLoader()
    this.adminService.fetchVehicleDamage().finally(() => {
      this.dialogService.hideLoader()
    })
  }

  applyFilter(filterValue: string) {
    this.items.filter = filterValue.trim().toLocaleLowerCase()
  }

  openNewDialog() {
    this.dialog.open<VehicleDamageDialogComponent, VehicleDamageDialogData>(VehicleDamageDialogComponent, {
      width: '800px',
      data: {
        action: 'new',
      },
    })
  }

  openEditDialog(damage: VehicleDamage) {
    this.dialog.open<VehicleDamageDialogComponent, VehicleDamageDialogData>(VehicleDamageDialogComponent, {
      width: '700px',
      data: {
        action: 'edit',
        damage,
      },
    })
  }

  onDelete(damage: any) {
    this.dialogService.showConfirm({ title: 'Delete item', msg: 'Are you sure?' }, () => {
      this.loading = true
      this.adminService.deleteVehicleDamage(damage.id).subscribe(
        (res) => {
          this.loading = false
          this.notificationService.success('Item deleted')
        },
        (err) => {
          this.loading = false
          this.notificationService.error()
        }
      )
    })
  }

  ngAfterViewInit() {
    this.items.sort = this.sort
    this.items.paginator = this.paginator
  }

  ngOnDestroy() {
    this.subscriptions.forEach((sub) => sub.unsubscribe())
  }
}
