import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { MatDialog } from '@angular/material/dialog'
import { MatPaginator } from '@angular/material/paginator'
import { MatSort } from '@angular/material/sort'
import { MatTableDataSource } from '@angular/material/table'
import { Subscription } from 'rxjs'
import { VehicleTemplate } from 'src/app/business/models/vehicleTemplate.models'
import { AdministrationService } from 'src/app/business/services/administration.service'
import { DialogService } from 'src/app/shared/dialogs/dialog.service'
import { NotificationService } from 'src/app/shared/notifications/notification.service'
import {
  VehicleTemplateDialogComponent,
  VehicleTemplateDialogData,
} from './vehicle-template-dialog/vehicle-template-dialog.component'

@Component({
  selector: 'app-vehicle-template',
  templateUrl: './vehicle-template.component.html',
  styleUrls: ['./vehicle-template.component.scss'],
})
export class VehicleTemplateComponent implements AfterViewInit, OnInit, OnDestroy {
  displayedColumns = ['preview', 'name', 'actions']
  pageSizeOptions: number[] = [12, 24, 50, 100]

  @ViewChild(MatSort) sort: MatSort
  @ViewChild(MatPaginator) paginator: MatPaginator

  subscriptions: Subscription[] = []
  items: MatTableDataSource<any>
  templates: VehicleTemplate[]
  loading: boolean

  constructor(
    public dialog: MatDialog,
    private dialogService: DialogService,
    public notificationService: NotificationService,
    private adminService: AdministrationService
  ) {
    this.items = new MatTableDataSource([])
    const sub = this.adminService.vehicleTemplates.subscribe((data) => {
      this.templates = data
      this.items.data = data
    })
    this.subscriptions.push(sub)
  }

  ngOnInit() {
    this.dialogService.showLoader()
    this.adminService.fetchVehicleTemplates().finally(() => {
      this.dialogService.hideLoader()
    })
  }

  applyFilter(filterValue: string) {
    this.items.filter = filterValue.trim().toLocaleLowerCase()
  }

  openNewDialog() {
    this.dialog.open<VehicleTemplateDialogComponent, VehicleTemplateDialogData>(VehicleTemplateDialogComponent, {
      width: '800px',
      data: {
        action: 'new',
      },
    })
  }

  openEditDialog(template: VehicleTemplate) {
    this.dialog.open<VehicleTemplateDialogComponent, VehicleTemplateDialogData>(VehicleTemplateDialogComponent, {
      width: '700px',
      data: {
        action: 'edit',
        template,
      },
    })
  }

  onDelete(template: any) {
    // console.log('👉 - VehicleTemplateComponent - template', template.id)
    this.dialogService.showConfirm({ title: 'Delete template', msg: 'Are you sure?' }, () => {
      this.loading = true
      this.adminService.deleteVehicleTemplate(template.id).subscribe(
        (res) => {
          this.loading = false
          this.notificationService.success('Template deleted')
        },
        (err) => {
          this.loading = false
          this.notificationService.error()
        }
      )
    })
  }

  ngAfterViewInit() {
    this.items.sort = this.sort
    this.items.paginator = this.paginator
  }

  ngOnDestroy() {
    this.subscriptions.forEach((sub) => sub.unsubscribe())
  }
}
