import { Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { ImageCroppedEvent, LoadedImage } from "ngx-image-cropper";
import { Subscription } from "rxjs";
import { VehicleTemplate } from "src/app/business/models/vehicleTemplate.models";
import { AdministrationService } from "src/app/business/services/administration.service";
import { NotificationService } from "src/app/shared/notifications/notification.service";

export interface VehicleTemplateDialogData {
  action: "new" | "edit";
  template?: VehicleTemplate;
}

@Component({
  selector: "app-vehicle-template-dialog",
  templateUrl: "./vehicle-template-dialog.component.html",
  styleUrls: ["./vehicle-template-dialog.component.scss"],
})
export class VehicleTemplateDialogComponent implements OnInit, OnDestroy {
  subscriptions: Subscription[] = [];
  isLoading = false;
  imageChangedEvent: any = "";
  croppedImage: any = null;
  templateName = "";
  templateNameError = false;
  selectedTemplate: VehicleTemplate;

  constructor(
    public dialogRef: MatDialogRef<VehicleTemplateDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: VehicleTemplateDialogData,
    private administrationService: AdministrationService,
    private notificationService: NotificationService
  ) {
    if (this.data.action === "edit") {
      this.selectedTemplate = this.data.template;
      this.templateName = this.selectedTemplate?.name;
    }
  }

  ngOnInit() {}

  fileChangeEvent(event: any): void {
    this.imageChangedEvent = event;
  }
  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64;
  }
  imageLoaded(image?: LoadedImage) {
    // show cropper
  }
  cropperReady() {
    // cropper ready
  }
  loadImageFailed() {
    // show message
  }

  onSubmit() {
    this.templateNameError = !this.templateName;
    if (!this.templateName) {
      this.notificationService.error("Template name is required");
      return;
    }

    if (!this.selectedTemplate?.imageUrl && !this.croppedImage) {
      this.notificationService.error("Image is required");
      return;
    }

    this.isLoading = true;
    (this.data.action === "new"
      ? this.administrationService.saveVehicleTemplate({ name: this.templateName, imageB64: this.croppedImage })
      : this.administrationService.updateVehicleTemplate(
          { name: this.templateName, imageB64: this.croppedImage ?? this.selectedTemplate.imageUrl },
          this.selectedTemplate.id
        )
    ).subscribe(
      (res) => {
        this.isLoading = false;
        this.dialogRef.close();
      },
      (err) => {
        this.isLoading = false;
        if (err.error?.message?.includes("expected `name` to be unique")) {
          this.notificationService.error("Name already exists");
          this.templateNameError = true;
        } else {
          this.notificationService.error();
        }
      }
    );
  }

  onCancel() {
    this.dialogRef.close();
  }

  ngOnDestroy() {
    this.subscriptions.forEach((sub) => sub.unsubscribe());
  }
}
