import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { BehaviorSubject, Observable } from 'rxjs'
import { map, take } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { Customer, CustomerBody } from '../models/customer.models'
import {
  VehicleDamage,
  VehicleDamageBody,
  VehicleTemplate,
  VehicleTemplateBody,
} from '../models/vehicleTemplate.models'

@Injectable({
  providedIn: 'root',
})
export class AdministrationService {
  private baseUrl = environment.apiUrl
  public vehicleTemplates: BehaviorSubject<VehicleTemplate[]> = new BehaviorSubject([])
  public vehicleDamage: BehaviorSubject<VehicleDamage[]> = new BehaviorSubject([])

  constructor(private http: HttpClient) {}

  fetchVehicleTemplates() {
    return new Promise((resolve, reject) => {
      this.http.get<any[]>(`${this.baseUrl}/administration/vehicleTemplate`).subscribe(
        (data) => {
          const dataParsed = data.map((temp) => {
            return { id: temp.id, name: temp.name, imageUrl: temp.imageB64 }
          })

          this.vehicleTemplates.next(dataParsed)
          resolve(data)
        },
        (err) => {
          reject(err)
        }
      )
    })
  }

  fetchVehicleDamage() {
    return new Promise((resolve, reject) => {
      this.http.get<any[]>(`${this.baseUrl}/administration/vehicleDamage`).subscribe(
        (data) => {
          this.vehicleDamage.next(data)
          resolve(data)
        },
        (err) => {
          reject(err)
        }
      )
    })
  }

  saveVehicleTemplate(body: VehicleTemplateBody) {
    return this.http.post<VehicleTemplate>(`${this.baseUrl}/administration/vehicleTemplate`, body).pipe(
      map((res) => {
        this.fetchVehicleTemplates()
        return res
      })
    )
  }

  saveVehicleDamage(body: VehicleDamageBody) {
    return this.http.post<VehicleTemplate>(`${this.baseUrl}/administration/vehicleDamage`, body).pipe(
      map((res) => {
        this.fetchVehicleDamage()
        return res
      })
    )
  }

  updateVehicleTemplate(body: VehicleTemplateBody, id: string) {
    return this.http.put<any>(`${this.baseUrl}/administration/vehicleTemplate/${id}`, body).pipe(
      map((res) => {
        this.fetchVehicleTemplates()
        return res
      })
    )
  }

  deleteVehicleTemplate(id: string) {
    return this.http.delete<any>(`${this.baseUrl}/administration/vehicleTemplate/${id}`).pipe(
      map((res) => {
        this.fetchVehicleTemplates()
        return res
      })
    )
  }

  deleteVehicleDamage(id: string) {
    return this.http.delete<any>(`${this.baseUrl}/administration/vehicleDamage/${id}`).pipe(
      map((res) => {
        this.fetchVehicleDamage()
        return res
      })
    )
  }

  getMostRecents() {
    return this.http.get<Customer[]>(`${this.baseUrl}/customers/mostRecents`)
  }
}
