import { Component, Inject, OnDestroy, OnInit } from '@angular/core'
import { FormBuilder, FormGroup, Validators } from '@angular/forms'
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog'
import { ImageCroppedEvent, LoadedImage } from 'ngx-image-cropper'
import { Subscription } from 'rxjs'
import { VehicleDamage, VehicleTemplate } from 'src/app/business/models/vehicleTemplate.models'
import { AdministrationService } from 'src/app/business/services/administration.service'
import { NotificationService } from 'src/app/shared/notifications/notification.service'

export interface VehicleDamageDialogData {
  action: 'new' | 'edit'
  damage?: any
}

@Component({
  selector: 'app-vehicle-damage-dialog',
  templateUrl: './vehicle-damage-dialog.component.html',
  styleUrls: ['./vehicle-damage-dialog.component.scss'],
})
export class VehicleDamageDialogComponent implements OnInit, OnDestroy {
  subscriptions: Subscription[] = []
  isLoading = false
  damageForm: FormGroup
  selectedDamage: VehicleDamage

  constructor(
    public dialogRef: MatDialogRef<VehicleDamageDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: VehicleDamageDialogData,
    private fb: FormBuilder,
    private administrationService: AdministrationService,
    private notificationService: NotificationService
  ) {
    if (this.data.action === 'edit') {
      this.selectedDamage = this.data.damage
    }

    this.damageForm = this.fb.group({
      name: [this.selectedDamage?.name ?? '', [Validators.required]],
      uid: [this.selectedDamage?.uid ?? '', [Validators.required]],
    })
  }

  ngOnInit() {}

  onSubmit() {
    if (this.damageForm.invalid) {
      return Object.values(this.damageForm.controls).forEach((control) => {
        if (control instanceof FormGroup) {
          Object.values(control.controls).forEach((control) => control.markAsTouched())
        } else {
          control.markAsTouched()
        }
      })
    } else {
      //save customer
      this.isLoading = true

      this.administrationService.saveVehicleDamage(this.damageForm.value).subscribe(
        (res) => {
          this.isLoading = false
          this.notificationService.success('Item added')
          this.dialogRef.close()
        },
        (err) => {
          this.isLoading = false
          if (err.error?.message?.includes('Damage key already exists')) {
            return this.notificationService.error('Damage ID already exists')
          }
          this.notificationService.error()
        }
      )
    }
  }

  onCancel() {
    this.dialogRef.close()
  }

  ngOnDestroy() {
    this.subscriptions.forEach((sub) => sub.unsubscribe())
  }
}
