import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'
import { VehicleTemplateComponent } from './vehicle-template/vehicle-template.component'
import { RouterModule } from '@angular/router'
import { SharedModule } from '../shared/shared.module'
import { VehicleTemplateDialogComponent } from './vehicle-template/vehicle-template-dialog/vehicle-template-dialog.component'
import { ImageCropperModule } from 'ngx-image-cropper'
import { VehicleDamageComponent } from './vehicle-damage/vehicle-damage.component'
import { VehicleDamageDialogComponent } from './vehicle-damage/vehicle-damage-dialog/vehicle-damage-dialog.component'

export const administrationRoutes = [
  {
    path: 'vehicle-templates',
    component: VehicleTemplateComponent,
  },
  {
    path: 'vehicle-damage',
    component: VehicleDamageComponent,
  },
]

@NgModule({
  declarations: [
    VehicleTemplateComponent,
    VehicleTemplateDialogComponent,
    VehicleDamageComponent,
    VehicleDamageDialogComponent,
  ],
  imports: [CommonModule, RouterModule.forChild(administrationRoutes), SharedModule, ImageCropperModule],
})
export class AdministrationModule {}
